.faqsec-box{
    box-sizing: border-box;
    width: 1000px;
    padding: 5px;
    padding-left: 5px;
    margin-top: 10px;
    border-radius: 5px;
    background-color: rgb(226, 255, 251);
}
.faqsec-ques{
    font-weight: 600;
    font-size: 15px;
}
.faqsec-an{
    font-size: 15px;
    line-height: 40px;
    box-sizing: border-box;
    padding: 2px;
    background-color: rgba(112, 169, 223, 0.253);
}
@media screen and (max-width: 480px) {
    .faqsec-box{
      width: 300px;
    }
}