.submit-box{
    display: flex;
    margin-top: 100px;
    padding: 50px;
    justify-content: space-between;
}
.submit-form-box{
    background-color: #ececec;
    padding: 10px;
    width:320px;
    box-shadow: 3px 3px 10px rgb(49, 47, 47);
    border-radius: 3px;
}
.formsubmitbtn{
    margin-top: 10px;
    height: 35px;
    color:white;
    font-weight: 700;
    font-size: 17px;
    border: none;
    background-color: #64aaa2;
}
.formsubmitbtn:hover{
    background-color: rgb(94, 255, 0);
    
}
.registerheadingname{
    color: #303c80;
    font-weight: 600;
    font-size: 25px;
}
.form-begin{
    display: flex;
    flex-direction: column;
    margin-top: 10px;
}
.submitinputheading{
    margin-top: 20px;
}
.submitformbox{
    font-size: 17px;
    margin-top: 10px;
    height: 30px;
    padding: 3px;
}
.right-data{
    width: 500px;
    padding: 5px;
}
.submitcourseheader{
    display: block;
    color:white;
    font-weight: 700;
    font-size: 20px;
    text-transform: capitalize;
    word-spacing: 3px;
    margin-top: 100px;
    text-decoration: underline;
}
.submitcourseoverview{
    line-height: 30px;
    color: aliceblue;
}
@media (max-width:480px) {
    .submit-box{
        display: flex;
        flex-direction: column;
    }
    .right-data{
        width:300px;
    }
    
}