.coursestartnow{
    background-color: #2e00fc;
    display: flex;
    padding-top: 10px;
    align-items: center;
    flex-wrap: wrap;
    flex-direction: column;
    padding-bottom: 10px;
    min-height: 500px;
} 
.course-heading{
    font-size: 25px;
    color: rgb(81, 255, 0);
    font-weight: 700;
}
.total-courses{ 
    margin-top: 30px;
    justify-content: space-between;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}
.courseslist{
    display: flex;
    justify-content: center;
}
.faqstartsnow{
    background-color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-bottom: 10px;
    min-height: 500px;
}
.faqsect{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.reviewstartnow{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-bottom: 10px;
    background-color: rgb(9, 255, 0);
}
.review-heading{
    color: blue;
    font-size: 25px;
    font-weight: 700;
}
@media screen and (max-width: 480px) {
    .total-courses{
        flex-direction: column;
    }
}