.review-box{
    background-color: rgba(35, 41, 38, 0.7);
    width: 1000px;
    box-sizing: border-box;
    padding: 3px;
    margin-top: 10px;
    border-radius: 5px;
}
.reviewer-name{
    font-weight: 600;
    font-size: 15px;
    color: aliceblue;
}
.review-text{
   line-height: 25px;
   font-size: 13px;
   color:aliceblue
}
.review-rating{
    color: gold;
    margin-left: 15px;
}
@media screen and (max-width: 480px) {
    .review-box{
        width: 300px;
    }
}