.nav-body{
    background-color:red;
    display: flex;
    height: 60px;

    align-items: center;
    padding-right: 10px;
    padding-left: 10px;
    justify-content: space-between;
}
.nav-ul{
    
    display: flex;
}
.logo{
    cursor: pointer;
    font-size: 30px;
    font-weight: bold;
    color: white
}
.nav-li{
    cursor: pointer;
    justify-content: space-around;
    display: flex;
    margin-left: 30px;
}
.nav-apply{
    color: white;
    border-radius: 3px;
    padding: 5px;
    padding-left: 10px;
    background-color: transparent;
    outline: none;
    border: 1px solid black;
    padding-right: 10px;
    cursor: pointer;
}
@media screen and (max-width: 480px) {
    .nav-body{
      height: 50px;
    }
    .logo{
        font-size: 15px;
    }
}