.course-box{
    background-color: white;
    /* height: 180px; */
    width: 330px;
    overflow: hidden;
    padding: 3px;
    border-radius: 10px;
    margin-top: 5px;

    margin-left: 15px;
    /* flex: 1; */
    cursor: pointer;
    box-sizing: border-box;
}
.course-top{
    height: 170px;
    display: flex;
}
.coursearrow{
    /* display: flex;
    justify-content: center;
    align-items: center; */
    width: 30px;
    height: 30px;
    color: #0051ff;
    margin-left: 17px;
    padding: 5px;
    margin-left: 285px;
    padding: 5px;
    margin-top: -15px;
    border-radius: 50%;
}
.coursedate{
    margin-left: 10px;
}
.coursesbottom{
    /* background-color: red; */
    height: 50px;
    margin-top: -20px;
}
.coursestart{
    color: #ec7e00;
    font-size: 20px;
    margin-top: -10px;
}
.coursestitle{
    font-size: 20px;
    float: none;
    line-height: 20px;
    z-index: auto;
    box-sizing: border-box;
    display: block;
    font-size: 15px;
    color: #ec7e00;
    font-weight: 600;
    margin-top: 20px;
    text-transform: uppercase;
}
.coursestime{
    background-color: #111827;
    color: white;
    border-radius: 5px;
    padding: 5px;
}
.coursestaken{
    color: #000;
    border-radius: 5px;
    padding: 4px;
    margin-left: 10px;
    background-color: white;
    border:1px solid #111827;
}
.coursename{
    box-sizing: border-box;
    float: none;
    margin-top: 1px;
    color: #111827;
    line-height: 32px
}
.coursesbtn{
    font-size: 1.2rem;
}
.coursesimg{
    margin-top: 10px;
}
.coursesdetails{
    width: fit-content;
    margin-top: 5px;
    height: 150px;
}
.course-box:hover{
    box-shadow: 0.5px 0.5px 20px rgb(182, 167, 167);
}
@media screen and (max-width: 480px) {
    .course-box{
        padding: 10px;
        width:340px;
        margin-left: 5px;
        margin-top: 10px;
    }
}
